// Expression searching and highlighting

export class Searcher {
  constructor(string, elements) {
    this.searchString = string.toLowerCase().trim();

    // Get any elements ( fu*# flux )
    this.elementsList = elements.map((e) => {
      return document.querySelectorAll(e);
    });
  }

  search = () => {
    // Public API
    this.elementsList.map((els) => {
      // Check all nodes
      els.forEach((e) => {
        // Check all node elements
        if (this.searchString != "") {
          // Marker found symbols
          e.innerHTML = this._findToken(this.searchString, e.innerText);
        } else {
          // Or delete all markers
          e.innerText = e.innerText
            .replace("<mark>", "")
            .replace("</mark>", "");
        }
      });
    });
  };

  _findToken = (token, string, result = "") => {
    if (result === "") {
      // Fist iteration

      // Let's find an exp in the given string
      let foundToken = string.match(new RegExp(token, "i"));
      let result = string; // Only here

      if (foundToken !== null) {
        // If we have found something ;)
        let offString = string.substring(foundToken.index + token.length);

        // Update result
        result =
          result.substring(0, foundToken.index) +
          this.mark(foundToken) +
          offString;

        // Recursive call itself ( next condition )
        this._findToken(token, offString, result);
      } else {
        // If we haven't found something =<
        return result;
      }
    } else {
      // Here we going find a token again
      let foundToken = string.match(new RegExp(token, "i"));

      // If we have again found some one
      if (foundToken != null) {
        // Get offseted string and result ( main actions here )
        let offString = string.replace(foundToken[0], this.mark(foundToken[0]));
        result = result.replace(string, offString);

        // Redeclare offString to give it itself ( recursive )
        offString = offString.substring(
          foundToken.index + this.mark(foundToken[0]).length
        );

        // Call itself ( Yamete kudasai )
        this._findToken(token, offString, result);
      } else {
        this.result = result;
        // Yeah, we've found it =)
      }
    }

    // Yep-yep
    return this.result;
  };

  // Can i stay here?
  mark = (token) => {
    return `<mark>${token}</mark>`;
  };
}

