export const rootReducer = (action, state) => {
  // Main (at point - root) reducer
  switch (action.type) {
    case "SWITCH_MENU_MODE": {
      // switch compact menu switch mode
      state.compactMenuCurtain = action.payload;
      break; // Uno
    }

    case "SWITCH_BODY_SCROLL": {
      switch (action.payload) {
        case "LOCK": {
          // Body scrollbar lock/unlock action api
          state.bodyScrollState = "LOCKED";
          document.getElementsByTagName("body")[0].classList.add("scroll-lock");
          break;
        }
        case "UNLOCK": {
          state.bodyScrollState = "UNLOCKED";
          document
            .getElementsByTagName("body")[0]
            .classList.remove("scroll-lock");
          break;
        }
      }
      return state;
    }

    case "SWITCH_SEARCHER_MODE": {
      // Searcher fullsize/compact mode switch action api
      state.searcher.searcherMode = action.payload;
      break;
    }

    case "CHANGE_SEARCHER_VALUE": {
      // Change value of the header searcher line (input)
      state.searcher.searcherValue = action.payload;
      break;
    }

    case "SEARCH_EXPRESSION": {
      // Search an expression in some html items
      action.payload.search(action.payload.expression, action.payload.items);
    }
  }

  return state;
};
