//Images
import fish from "./img/fish.jpeg";
import logo from "./img/logo192.png";

// Reducers
import { rootReducer } from "./RootReducer";

// Modules
import { Searcher } from "./components/modules/Searcher";

// Observer registration
let invokeSubscriber = () => {
  console.log("No one subscribed me :<");
};

export const subscribe = (observer) => {
  invokeSubscriber = observer;
};

// Inital state
const initalState = {
  location: {
    //Public location config
    home: "dooboox/",
    content: "dooboox/content",
    authors: "dooboox/authors",
    about: "dooboox/about",
  },

  images: {
    fish: fish,
    logo: logo,
  },

  bodyScrollState: "UNLOCKED", // Lock/unlock body scrollbar
  mobileScreenWidth: 590, // Mobile screen resolution (px)
  compactMenuCurtain: "compact-menu_curtain", // Show/hide cmenu curtain

  searcher: {
    searcherMode: "COMPACT", // Switch compact or fullsize mode (top-righg corner)
    searcherValue: "",
    search(expression, elements) {
      return new Searcher(expression, elements).search();
    },
  },

  menuMode() {
    // Toggle menu mode
    return document.body.clientWidth <= this.mobileScreenWidth
      ? "COMPACT" // Set compact menu
      : "FULLSIZE"; // Set fullsize menu
  },

  actionCreator(type, payload) {
    // Useful api for any spa items
    store.dispatch({
      type: type,
      payload: payload,
    });
  },
};

// Store creator
const createStore = (reducer, state = initalState) => {
  return {
    state,

    getState() {
      return this.state;
    },

    // You can add a new reducer here
    reducers: [rootReducer],

    dispatch(action) {
      this.reducers.forEach((r) => {
        this.state = r(action, this.state);
      });
      invokeSubscriber(this.state);
    },
  };
};

// Exporting of the created store
export const store = createStore(rootReducer);
